.footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
    padding-top: 20px;
}

.footer-logo {
    display: flex;
    align-items: center;
    gap: 20px;
}

.footer-logo p {
    color: #383838;
    font-size: 30px;
    font-weight: 700;  
}

.footer-links {
    display: flex;
    list-style: none;
    gap: 30px;
    color: #252525;
    font-size: 16px;
}

.footer-links li {
    cursor: pointer;
}

.footer-social-icons {
    display: flex;
    gap: 20px;
}

.footer-icons-container {
    padding: 6px;
    padding-bottom: 6px;
    background-color: #f6f6f6;
    border: 1px solid #ebebeb;
    /* display: flex; */
}

.footer-copyright {
   display: flex;
   flex-direction: column;
   align-items: center;
   gap: 30px;
   width: 100%;
   margin-bottom: 30px;
   color: #1a1a1a;
   font-size: 18px; 
}

.footer-copyright hr {
    width: 80%;
    border: none;
    border-radius: 10px;
    height: 1px;
    background: #c7c7c7;
}

@media (max-width:810px) {
    .footer {
        gap: 20px;
    }

    .footer-logo {
        gap: 10px;
        align-items: end;
    }

    .footer-logo img {
        width: 40px;
    }
    
    .footer-logo p {
        font-size: 25px;
    }

    .footer-links {
        font-size: 14px;
        gap: 10px;
    }

    .footer-social-icons img {
        width: 22px;
    }

    .footer-copyright {
        font-size: 13px;
    }
}